<template>
  <div class="editable-cell">
    <!-- {{runeditkey}}
    {{currentkey}}-->
    <div v-if="editable" class="editable-cell-input-wrapper">
      <a-input :value="value" @change="handleChange" @pressEnter="check"/>
      <a-icon type="check-circle-o" class="editable-cell-icon-check" @click="check"/>
      <a-icon type="close-circle-o" class="editable-cell-icon-close" @click="close"/>
    </div>
    <div v-else class="editable-cell-text-wrapper">
      {{value || ' '}}
      <a-icon v-if="runeditkey===currentkey" type="edit" class="editable-cell-icon" @click="edit"/>
      <a-icon v-else-if="runeditkey===999" type="edit" class="editable-cell-icon" @click="edit"/>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    text: String,
    currentkey: Number,
    runeditkey: Number
  },
  mounted() {
    console.log("text", this.text);
    // this.$root.$on("send", text => {
    //   console.log("fire.........");
    //   this.value = this.text;
    //   //this.editable = false;
    //   //this.$emit("iseditable", 999);
    // });
  },
  data() {
    return {
      value2: this.text,
      editable: false
    };
  },
  computed: {
    // value2: function() {
    //   return this.text;
    // },
    value: {
      get: function() {
        return this.value2;
      },
      set: function(newValue) {
        this.value2 = newValue;
      }
    }
  },
  methods: {
    handleChange(e) {
      const value = e.target.value;
      this.value = value;
    },
    check() {
      this.editable = false;
      this.$emit("iseditable", 999);
      this.$emit("change", this.value);
    },
    close() {
      this.value = this.text;
      this.editable = false;
      this.$emit("iseditable", 999);
    },
    edit() {
      this.editable = true;
      this.$emit("iseditable", this.currentkey);
    }
  }
};
</script>
<style>
.editable-cell-icon-close {
  position: absolute;
  top: 20px;
  right: 3px;
}
</style>
