<template>
  <div>
    <!-- <a-button class="editable-add-btn" @click="handleAdd">Add</a-button> -->
    <a-table
      :columns="columns"
      :dataSource="data"
      :pagination="pagination"
      :loading="loading"
      @change="handleTableChange"
      bordered
    >
      <template slot="title" slot-scope="currentPageData">Payout Data</template>
      <template slot="description" slot-scope="text, record,index">
        <!-- {{text}} -->
        <EditableCell
          :text="text"
          :currentkey="index"
          :runeditkey="runeditkey"
          :update="update"
          @iseditable="CheckEdit"
          @change="onCellChange(record.key, 'Description')"
        />
      </template>
    </a-table>
  </div>
</template>
<script>
import reqwest from "reqwest";
import Papa from "papaparse";
import moment from "moment";
import EditableCell from "./EditableCell.vue";
const columns = [
  {
    title: "ID",
    dataIndex: "ID",
    width: "17%",
    align: "center"
  },
  {
    title: "Name",
    dataIndex: "Name",
    //sorter: true,
    width: "10%"
    //scopedSlots: { customRender: "name" }
  },
  {
    title: "Description",
    dataIndex: "Description",
    //sorter: true,
    width: "30%",
    scopedSlots: { customRender: "description" }
  },
  {
    title: "Amount",
    dataIndex: "Amount",
    //sorter: true,
    width: "10%",
    customRender: (text, record, index) => {
      return "$ " + text;
    }
    //scopedSlots: { customRender: "name" }
  },
  {
    title: "Date",
    dataIndex: "Date",
    sorter: true,
    width: "10%",
    customRender: (text, record, index) => {
      return moment(text).format("MM/DD/YYYY HH:mm");
    },
    sorter: (a, b) => {
      return moment(a.Date || 0).unix() - moment(b.Date || 0).unix();
    }
    //scopedSlots: { customRender: "name" }
  }
];

export default {
  components: {
    EditableCell
  },
  mounted() {
    this.fetch();
  },
  data() {
    return {
      data: [],
      pagination: {},
      loading: false,
      iseditrun: false,
      runeditkey: 999,
      update: 2,
      columns
    };
  },
  methods: {
    onCellChange(key, dataIndex) {
      console.log("onCellChange", dataIndex);
      return value => {
        console.log("onCellChange", value);
        const dataSource = [...this.dataSource];
        const target = dataSource.find(item => item.key === key);
        if (target) {
          target[dataIndex] = value;
          this.dataSource = dataSource;
        }
      };
    },
    CheckEdit(dataIndex) {
      console.log("CheckEdit 22", dataIndex);
      if (dataIndex) {
        this.iseditrun = true;
        this.runeditkey = dataIndex;
      } else {
        this.iseditrun = false;
        this.runeditkey = dataIndex;
      }
    },
    handleTableChange(pagination, filters, sorter) {
      console.log(pagination);
      console.log(filters);
      console.log(sorter);
      const pager = { ...this.pagination };
      pager.current = pagination.current;
      this.pagination = pager;
      this.update++;
      this.$set(this.data);
      //this.$root.$emit("send", "bye");
      // this.fetch({
      //   results: pagination.pageSize,
      //   page: pagination.current,
      //   sortField: sorter.field,
      //   sortOrder: sorter.order,
      //   ...filters
      // });
    },
    fetch(params = {}) {
      console.log("params1:", params);
      // this.loading = true;
      // reqwest({
      //   url: "https://randomuser.me/api",
      //   method: "get",
      //   data: {
      //     results: 10,
      //     ...params
      //   },
      //   type: "json"
      // }).then(data => {
      //   console.log("ddddd", data);
      //   const pagination = { ...this.pagination };
      //   // Read total count from server
      //   // pagination.total = data.totalCount;
      //   pagination.total = 200;
      //   this.loading = false;
      //   //this.data = data.results;
      //   this.pagination = pagination;
      // });
      var $this = this;
      this.loading = true;
      Papa.parse(
        "https://raw.githubusercontent.com/HirenPlutus/vue-demo/master/src/assets/PaymentsData.csv",
        {
          download: true,
          header: true,
          complete: function(results) {
            console.log("Finished11:", results.data);
            $this.data = results.data;
            const pagination = { ...$this.pagination };
            pagination.total = results.data.length;
            $this.pagination = pagination;
            console.log("$this.data", $this.data);
            console.log("this.data", this.data);
            $this.loading = false;
          }
        }
      );
    }
  }
};
</script>
<style>
.editable-cell {
  position: relative;
}

.editable-cell-input-wrapper,
.editable-cell-text-wrapper {
  padding-right: 24px;
}

.editable-cell-text-wrapper {
  padding: 5px 24px 5px 5px;
}

.editable-cell-icon,
.editable-cell-icon-check {
  position: absolute;
  right: 0;
  width: 20px;
  cursor: pointer;
}

.editable-cell-icon {
  line-height: 18px;
  display: none;
}

.editable-cell-icon-check {
  line-height: 28px;
}

.editable-cell:hover .editable-cell-icon {
  display: inline-block;
}

.editable-cell-icon:hover,
.editable-cell-icon-check:hover {
  color: #108ee9;
}

.editable-add-btn {
  margin-bottom: 8px;
}
.ant-table-title {
  font-size: 20px;
  font-weight: 800;
}
</style>
